/* src/components/Instructions.css */

.instructions-container {
  background-color: #f0f8ff; /* Light blue background */
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: auto;
  text-align: center;
}

h2 {
  font-size: 2.8rem;
  color: #0077b6; /* Soft blue color for heading */
  margin-bottom: 20px;
}

.intro-text {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 30px;
}

.steps-container {
  text-align: left;
  margin-bottom: 30px;
}

.steps-list {
  list-style-type: decimal;
  padding-left: 20px;
  font-size: 1.1rem;
  color: #333;
}

.instruction-text {
  font-size: 1.1rem;
  color: #555;
  margin-top: 10px;
}

.navigation-buttons {
  margin-top: 30px;
}

.go-to-upload-button {
  padding: 12px 30px;
  background-color: #4caf50; /* Green button */
  color: white;
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.go-to-upload-button:hover {
  background-color: #388e3c; /* Darker green on hover */
}
